import axios from "axios";
import pkg from "agora-token";
const { RtcTokenBuilder, RtcRole } = pkg;

const APP_ID = process.env.AGORA_APP_ID;
const APP_CERTIFICATE = process.env.AGORA_APP_CERTIFICATE;
const API_KEY = process.env.AGORA_API_KEY;
const API_SECRET = process.env.AGORA_API_SECRET;

function assertAgoraEnv() {
  if (!APP_ID || !APP_CERTIFICATE || !API_KEY || !API_SECRET) {
    throw new Error(
      "Missing Agora credentials. Please set AGORA_APP_ID, AGORA_APP_CERTIFICATE, AGORA_API_KEY, AGORA_API_SECRET."
    );
  }
}

// Generate RTC token using Agora's official SDK
// When using string UIDs, we need to use buildTokenWithAccount
export function generateToken(channelName: string, uid: number | string) {
  assertAgoraEnv();
  const role = RtcRole.PUBLISHER;
  const expirationTimeInSeconds = 3600;
  const currentTimestamp = Math.floor(Date.now() / 1000);
  const privilegeExpiredTs = currentTimestamp + expirationTimeInSeconds;

  // Use buildTokenWithAccount for string UIDs, buildTokenWithUid for numeric UIDs
  const token =
    typeof uid === "string"
      ? RtcTokenBuilder.buildTokenWithAccount(
          APP_ID,
          APP_CERTIFICATE,
          channelName,
          uid,
          role,
          privilegeExpiredTs
        )
      : RtcTokenBuilder.buildTokenWithUid(
          APP_ID,
          APP_CERTIFICATE,
          channelName,
          uid,
          role,
          privilegeExpiredTs
        );

  return token;
}

// Start Conversational AI agent using Agora Conversational AI Engine
export async function startAgent(
  channelName: string,
  userId: string | number,
  context: string,
  token: string
) {
  assertAgoraEnv();
  try {
    const auth = Buffer.from(`${API_KEY}:${API_SECRET}`).toString("base64");

    const response = await axios.post(
      `https://api.agora.io/api/conversational-ai-agent/v2/projects/${APP_ID}/join`,
      {
        name: "ecommerce_ai_agent",
        properties: {
          channel: channelName,
          token: token,
          agent_rtc_uid: "0",
          remote_rtc_uids: [String(userId)],
          enable_string_uid: true,
          idle_timeout: 120,
          llm: {
            url:
              process.env.LLM_URL ||
              "https://api.openai.com/v1/chat/completions",
            api_key: process.env.LLM_API_KEY || "",
            system_messages: [
              {
                role: "system",
                content: context,
              },
            ],
            greeting_message:
              "Hello! I'm here to help you with this product. Ask me anything about features, reviews, or specifications!",
            failure_message:
              "I'm sorry, I can only assist with questions related to this product. Could you please ask about our product?",
            max_history: 10,
            params: {
              model: process.env.LLM_MODEL || "gpt-4o-mini",
            },
          },
          asr: {
            language: "en-US",
          },
          tts: {
            vendor: "microsoft",
            params: {
              key: process.env.TTS_API_KEY || "",
              region: process.env.TTS_REGION || "eastus",
              voice_name: "en-US-AndrewMultilingualNeural",
            },
          },
        },
      },
      {
        headers: {
          Authorization: `Basic ${auth}`,
          "Content-Type": "application/json",
        },
      }
    );

    return response.data;
  } catch (error) {
    const err: any = error;
    const status = err?.response?.status;
    const data = err?.response?.data;

    // Handle 409 Conflict - agent already running, return existing agent info
    if (status === 409 && data?.agent_id) {
      console.log(
        "[startAgent] Agent already running, returning existing agent:",
        data.agent_id
      );
      return {
        agent_id: data.agent_id,
        status: data.status || "RUNNING",
        create_ts: data.create_ts,
        ...data,
      };
    }

    const message =
      data?.detail ||
      data?.message ||
      err?.message ||
      "Unknown error starting Agora agent";
    const details = { status, data };
    throw new Error(
      `${message}${status ? ` (status ${status})` : ""}::${JSON.stringify(
        details
      )}`
    );
  }
}

// Process chat message with Agora Conversational AI
export async function processChatMessage(
  message: string,
  context: string,
  productId: string
) {
  try {
    const offTopicKeywords = [
      "bake",
      "baking",
      "cooking",
      "recipe",
      "cake",
      "how to cook",
      "weather",
      "sports",
      "politics",
      "news",
      "unrelated",
      "off topic",
    ];

    const messageLower = message.toLowerCase();
    const isOffTopic = offTopicKeywords.some((keyword) =>
      messageLower.includes(keyword)
    );

    if (isOffTopic) {
      return {
        response:
          "I'm here to help you with this product. Would you like to know about its features, reviews, or specifications?",
        isOffTopic: true,
      };
    }

    const response = await axios.post(
      "https://api.agora.io/v1/conversational-ai/chat",
      {
        message: message,
        context: context,
        product_id: productId,
        app_id: APP_ID,
      },
      {
        headers: {
          Authorization: `Basic ${Buffer.from(
            `${API_KEY}:${API_SECRET}`
          ).toString("base64")}`,
          "Content-Type": "application/json",
        },
      }
    );

    return {
      response: response.data.message || response.data.response,
      isOffTopic: false,
    };
  } catch (error) {
    return {
      response:
        "I'm having trouble processing that right now. Could you rephrase your question about the product?",
      isOffTopic: false,
    };
  }
}
